import React, {useEffect, useState} from 'react';
import {
    Player,
    ControlBar,
} from 'video-react'
import 'video-react/dist/video-react.css';
import classes from './Intro.module.css';

const quotes = [
    '"All ideas grow out of other ideas Anish Kapoor, Artist',
    'I write because I dont know what I think until I read what I say.Flannery OConnor, Author',
    'What we call chaos is just patterns we havent recognized. What we call random is just patterns we cant decipher.  Chuck Palahniuk, Journalist ',
    'As soon as a thought darts, I write it down. Thomas Hobbs, Philosopher'
]

const Intro = (props) => {
    const [currentQuote, setCurrentQuote] = useState('')

    useEffect(() => {
            const quoteNumber = Math.floor(Math.random() * quotes.length);
            setCurrentQuote(quotes[quoteNumber])
        }
    )

    return (
        <div className={classes.intro}>
            <h1 className={classes.quote}>{currentQuote}</h1>
            <div className={classes.videoWrapper}>
                <Player className={classes.video} autoPlay playsInline muted>
                    <source src={require('../../../assets/thinkfully.mp4')}/>
                    <ControlBar disabled>
                    </ControlBar>
                </Player>
            </div>
        </div>
    );
};

export default Intro;