import React from 'react';

import classes from './Hexagon.module.css';

const Hexagon = (props) => {
    return (
        <div className={classes.hexagon + ' ' + classes[props.popup]}>
            <div className={classes.box}>
                <div className={classes.textcontent}>
                    {props.children}
                </div>
            </div>
        </div>
    );
};

export default Hexagon;