import React, {useContext} from 'react';

import AddHex from "../AddHex/AddHex";
import classes from './Sidebar.module.css';
import ColorPicker from "../ColorPicker/ColorPicker";
import MultiSelect from "../MultiSelect/MultiSelect";
import Booster from "../Booster/Booster";
import Copy from "../Copy/Copy";
import Sort from "../Sort/Sort";
import Trash from "../Trash/Trash";
import Bold from "../Bold/Bold";
import Save from "../Save/Save";
import Logout from "../Logout/Logout";
import AuthContext from "../../../store/auth-context";

const Sidebar = (props) => {
    const authCtx = useContext(AuthContext)
    return (
        <section className={classes.navbar}>
            <img className={classes.logo} src={require('../../../assets/logo.png')}/>
            <AddHex activeHexes={props.activeHexes} setActive={props.setActive}/>
            <div className={classes.iconWrapper}>
                <Copy removeSelected={props.removeSelected} />
                <ColorPicker selectMode={props.selectMode} multiColorHandler={props.multiColorHandler} setColor={props.setColor}/>
                {/*<Sort/>*/}
                {/*<Booster/>*/}
                <MultiSelect removeSelected={props.removeSelected} />
                <Bold setBold={props.setBold} enableBoldItem={props.enableBoldItem}/>
            </div>
            <Trash deleteAllSelected={props.deleteAllSelected} removeSelected={props.removeSelected} />
            <div className={classes.iconWrapper}>
                {authCtx.userName !== 'undefined' && authCtx.userName !== false &&  <Save saveDataHandler={props.saveDataHandler} />}
            <Logout clearDataHandler={props.clearDataHandler} />
            </div>
        </section>
    );
};

export default Sidebar;